$main-yellow: #f4ff00;
$main-black: #030200;
$main-violet: #68009b;
$main-gray: #e5e5e5;
@font-face {
    font-family: 'menu font';
    src: url('../fonts/acme-regular.ttf');
}

@font-face {
    font-family: 'logo font';
    src: url('../fonts/alpha_echo.ttf');
}

@font-face {
    font-family: 'title font';
    src: url('../fonts/open_sans.ttf');
}

@mixin btn-style {
    border: 1px solid $main-gray;
    border-radius: 3px;
    line-height: 1.5vw;
    cursor: pointer;
    font-family: "menu font";
    &:hover {
        background-color: $main-violet;
        color: $main-yellow;
    }
}
@mixin title-style {
    text-align: center;
    text-transform: uppercase;
    font-family: "title font";
    font-size: 1.85vw;
}

.test {
    height: 200vh;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    color:$main-black;
}

.container {
    width: 96.5%;
    margin-left: auto;
}

.header {
    background-color: #000;
    ul {
        list-style-type: none;
    }
    a {
        text-decoration: none;
        color: $main-yellow;
    }
    h1 {
        color: $main-violet;
        background-color: $main-yellow;
        margin: 0;
        text-align: center;
        line-height: 3.1vw;
        font-size: 2.7vw;
        font-family: "logo font";
    }
}

.header__inner {
    display: grid;
    grid-template-columns: 20% 30% 30% 13% 7%;
    grid-template-rows: 3vw;
    align-items: center;
}

.header__menu {
    display: grid;
    grid-template-columns: repeat(3, minmax(35px, 1fr));
    grid-template-rows: 3vw;
    text-transform: uppercase;
    font-size: 0.95vw;
    text-align: center;
    padding-left: 0;
    margin: 0;
    font-family: "menu font";
    >li {
        &:first-child {
            border-left: 2px solid $main-gray;
        }
        line-height: 3vw;
        border-right:2px solid $main-gray;
    }
}

.header__nav {
    height: 100%;
}

.header__submenu1,
.header__submenu2,
.header__submenu3 {
    transform: translateY(-31vw);
    background-color: #000;
    padding-left: 0;
    opacity: 0;
    transition: .5s;
    a {
        display: block;
        width: 100%;
        height: 100%;
    }
    li {
        border-top: 1px solid $main-violet;
        &:hover {
            background-color: $main-yellow;
            a {
                color: $main-black;
            }
        }
    }
}

.header__submenu3 {
    transform: translateY(-18vw);
}

.header__menu-li1:hover,
.header__menu-li2:hover,
.header__menu-li3:hover {
    background-color: $main-yellow;
    z-index: 2;
    &>a {
        color: $main-black;
    }
}

.header__menu-li1:hover>.header__submenu1 {
    transform: translateY(0);
    opacity: 1;
    transition: .5s;
}

.header__menu-li3:hover>.header__submenu3 {
    transform: translateY(0);
    opacity: 1;
    transition: .5s;
}

.header__search {
    position: relative;
    -webkit-appearance: none;
    width: 90%;
    margin: 0 auto;
    display: flex;
    input {
        border-radius: 5px;
        border: none;
        width: 100%;
        font-size: 1vw;
        background-color: $main-gray;
        &:focus {
            background-color: #fff;
        }
    }
    .header__button-search {
        -webkit-appearance: none;
        border: none;
        background: url('../img/search.svg') 0 0 no-repeat;
        height: 85%;
        width: calc(100% / 15);
        position: absolute;
        top: 5%;
        right: .5%;
        outline: none;
    }
}

.header__registation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .header__sign-up {
        background-color: $main-black;
        color: $main-yellow;
        @include btn-style;
    }
    .header__create-account {
        background-color: $main-yellow;
        color: $main-black;
        @include btn-style;
        font-size: 0.9vw;
    }
}

.header__upload {
    text-align: center;
    font-weight: 700;
    font-size: 1vw;
    line-height: 3.1vw;
    &:hover {
        background-color: $main-yellow;
        color: $main-black;
    }
}

.aside {
    width: 3.5%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000000;
    height: 100vh;
    z-index: 2;
    a {
        text-decoration: none;
        color: $main-yellow;
        width: 2.6vw;
        margin: 0 auto;
    }
}

.aside__menu {
    list-style-type: none;
    display: grid;
    grid-template-rows: repeat(10, minmax(4.1vw, 1fr));
    height: 100vh;
    padding-left: 0;
    margin-top: 0;
}

.aside__menu-item1 {
    &>a {
        display: block;
        height: 100%;
    background: url(../img/top10.svg) no-repeat right center;
    margin-left: auto;
    margin-right: 0;
    width: 3.1vw;
    }
    .aside__submenu-1 {
        list-style-type: none;
        padding: 0;
        margin-right:-10vw;
        transform: translate(26%, -33vw);
        transition: .4s;
        background-color: $main-black;
        text-align: center;
        text-transform: uppercase;
        font-size: 1vw;
        border: 1px solid $main-violet;
        border-radius: 0 15px 15px 0;
        li {
            z-index: 2;
            border-top: 1px solid $main-violet;
        }
        a {
            display: block;
            width: 100%;
            height: 100%;
            padding: 0.4vw 0;
            
        }
    }
    
    &:hover>.aside__submenu-1 {
        transition: .4s;
        transform: translate(25%, -9.5%);
    }
}

.aside__sumbenu1-item1 {
    border-radius: 0 15px 0 0;
    &:hover {
        border-radius: 0 15px 0 0;
    }
}

.aside__sumbenu1-item1:hover,
.aside__sumbenu1-item2:hover,
.aside__sumbenu1-item3:hover,
.aside__sumbenu1-item4:hover,
.aside__sumbenu1-item5:hover,
.aside__sumbenu1-item6:hover,
.aside__sumbenu1-item7:hover,
.aside__sumbenu1-item8:hover,
.aside__sumbenu1-item9:hover {
    background-color: $main-yellow;
    a {
        color: $main-black;
    }
}

.aside__sumbenu1-item9:hover {
    border-radius: 0 0 15px 0;
}
.aside__menu-item1,  .aside__menu-item2,.aside__menu-item3, .aside__menu-item4,.aside__menu-item5, .aside__menu-item6,
.aside__menu-item7,.aside__menu-item8,.aside__menu-item9,.aside__menu-item10 {
    &:hover {
        background-color: $main-violet;
    }
}
.aside__menu-item2 {
    a {
        display: block;
        height: 100%;
        background: url('../img/rock_and_roll.svg') no-repeat center center;
    }
}

.aside__menu-item3 {
