.albums__tracks1,.albums__tracks2 ,.albums__tracks3,.albums__tracks4,.albums__tracks5,.albums__tracks6,
.albums__tracks7,.albums__tracks8,.albums__tracks9,.albums__tracks10,.albums__tracks11,.albums__tracks12,
