        text-align: center;
        text-transform: uppercase;
        font-family: "title font";
    }
    label {
        display: block;
        position: relative;
        min-height: 3vw;
        min-width: 2vw;
        float:left;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            width: 2vw;
            height: 2vw;
            box-shadow: 2px 2px 0 1px $main-black;
            transform: rotate(135deg);
            top: 16%;
            left: 40%;
        }
        &:hover:before {
            box-shadow:2px 2px 0 1px $main-yellow;
        }
    }
}
