.albums__item4:checked ~ .albums__tracks4,.albums__item5:checked ~ .albums__tracks5,.albums__item6:checked ~ .albums__tracks6,
.albums__item7:checked ~ .albums__tracks7,.albums__item8:checked ~ .albums__tracks8,.albums__item9:checked ~ .albums__tracks9,
.albums__item10:checked ~ .albums__tracks10,.albums__item11:checked ~ .albums__tracks11,.albums__item12:checked ~ .albums__tracks12,
