}
.contacts {
    h3 {
        @include title-style();
    }

}

.contacts__inner {
    display:grid;
    grid-template-columns: 50% 50%;
}
.contacts__info {
    display:flex;
    flex-direction: column;
    padding: 0 5%;
    ul {
        margin-top:0;
        list-style-type: none;
        padding-left: 0;
    }
    a {
        color:$main-black;
        text-decoration: none;
    }
    li {
        line-height: 25px;
    }
    ul:first-child {
        li {
            height: 25%;
        }
    }
}
.contacts__info-top {
    display: grid;
    grid-template-columns: 48% 52%;
    margin: 0 auto;
    span {
        font-weight: 700;
    }
    a:hover {
        color:$main-violet;
    }
}
.contacts__social-logo {
    width: 15px;
    padding-right: 1%;
}
.contacts__map {
    iframe {
        border: 0;
        width: 90%;
        height: 100%;
    }
}
.contacts__form-fields {
    display: grid;
    grid-template-columns: repeat(2, 48.8%);
    grid-gap: 1vw;
    input {
        line-height: 25px;
        text-align: center;
        outline: none;
        border-radius: 25px;
        background-color: $main-gray;
        &:focus {
            background-color: #fff;
        }
    }
    select {
        border-radius: 5px;
        outline: none;
        height: 25px;
        align-self: center;
    }

}
.contacts__info-bottom {
    textarea {
        width: 100%;
        height: 11vw;
        outline: none;
        border-radius: 25px;
        margin: 1vw 0 ;
        padding: 13px;
        resize: none;
        background-color: $main-gray;
        &:focus {
            background-color: #fff;
        }
    }
    form {
        background-color: $main-black;
        border-radius: 25px;
        padding: 2%;
    }
}
.contacts__form-buttons {
    display: grid;
    grid-template-columns: repeat(2, 30%);
    justify-content: space-around;
    input {
        height: 25px;
        outline: none;
        
        &:first-child {
            background-color: $main-black;
            border:1px solid #fff;
            color:$main-yellow;
        }
        &:nth-child(2){
            background-color: $main-yellow;
            border:1px solid #fff;
            color:$main-black;
        }
        @include btn-style();
    }
}
